'use client';

import { useState, useEffect, useCallback } from 'react';

interface ModalProps {
    isOpen: boolean;
    onClose: () => void;
    title: string;
    children: React.ReactNode;
}

export function Modal({ isOpen, onClose, title, children }: ModalProps) {
    // Close on Escape key
    useEffect(() => {
        const handleEscape = (e: KeyboardEvent) => {
            if (e.key === 'Escape') onClose();
        };
        if (isOpen) {
            document.addEventListener('keydown', handleEscape);
            document.body.style.overflow = 'hidden';
        }
        return () => {
            document.removeEventListener('keydown', handleEscape);
            document.body.style.overflow = 'unset';
        };
    }, [isOpen, onClose]);

    if (!isOpen) return null;

    return (
        <div
            className="fixed inset-0 z-50 flex items-center justify-center"
            role="dialog"
            aria-modal="true"
            aria-labelledby="modal-title"
        >
            {/* Backdrop */}
            <div
                className="absolute inset-0 bg-black/80 backdrop-blur-sm"
                onClick={onClose}
                aria-hidden="true"
            />

            {/* Modal Panel */}
            <div className="relative z-10 w-full max-w-md mx-4 bg-[#111] border border-[#333] rounded-2xl shadow-2xl animate-modal-in">
                {/* Header */}
                <div className="flex items-center justify-between p-6 border-b border-[#222]">
                    <h2 id="modal-title" className="text-xl font-bold text-white">{title}</h2>
                    <button
                        onClick={onClose}
                        className="p-2 rounded-lg hover:bg-[#222] text-[#9A9A9A] hover:text-white transition-colors"
                        aria-label="Close modal"
                    >
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        </svg>
                    </button>
                </div>

                {/* Content */}
                <div className="p-6">
                    {children}
                </div>
            </div>
        </div>
    );
}

interface AddLinkModalProps {
    isOpen: boolean;
    onClose: () => void;
    onAdd: (title: string, url: string) => void;
}

export function AddLinkModal({ isOpen, onClose, onAdd }: AddLinkModalProps) {
    const [title, setTitle] = useState('');
    const [url, setUrl] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!title.trim() || !url.trim()) return;

        setIsSubmitting(true);
        try {
            await onAdd(title, url);
            setTitle('');
            setUrl('');
            onClose();
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleClose = useCallback(() => {
        setTitle('');
        setUrl('');
        onClose();
    }, [onClose]);

    return (
        <Modal isOpen={isOpen} onClose={handleClose} title="Add New Link">
            <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                    <label htmlFor="link-title" className="block text-sm font-medium text-[#9A9A9A] mb-2">
                        Title
                    </label>
                    <input
                        id="link-title"
                        type="text"
                        value={title}
                        onChange={e => setTitle(e.target.value)}
                        placeholder="My Awesome Link"
                        className="w-full px-4 py-3 bg-[#0a0a0a] border border-[#333] rounded-xl text-white placeholder-[#666] focus:border-[#00C853] focus:ring-1 focus:ring-[#00C853] outline-none transition-colors"
                        autoFocus
                        required
                    />
                </div>

                <div>
                    <label htmlFor="link-url" className="block text-sm font-medium text-[#9A9A9A] mb-2">
                        URL
                    </label>
                    <input
                        id="link-url"
                        type="url"
                        value={url}
                        onChange={e => setUrl(e.target.value)}
                        placeholder="https://example.com"
                        className="w-full px-4 py-3 bg-[#0a0a0a] border border-[#333] rounded-xl text-white placeholder-[#666] focus:border-[#00C853] focus:ring-1 focus:ring-[#00C853] outline-none transition-colors"
                        required
                    />
                </div>

                <div className="flex gap-3 pt-4">
                    <button
                        type="button"
                        onClick={handleClose}
                        className="flex-1 px-4 py-3 bg-[#222] hover:bg-[#333] text-white rounded-xl font-medium transition-colors"
                    >
                        Cancel
                    </button>
                    <button
                        type="submit"
                        disabled={isSubmitting || !title.trim() || !url.trim()}
                        className="flex-1 px-4 py-3 bg-[#00C853] hover:bg-[#00E676] disabled:bg-[#00C853]/50 disabled:cursor-not-allowed text-black font-bold rounded-xl transition-colors"
                    >
                        {isSubmitting ? 'Adding...' : 'Add Link'}
                    </button>
                </div>
            </form>
        </Modal>
    );
}
